.footer{
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    text-transform: uppercase;
    &:not(:last-child){
      margin-right: 35px;
    }
  }
  &__address,
  &__mail{
    font-weight: 500;
    font-size: 13px;
    line-height: 180%;
    text-transform: uppercase;
  }
  &__address{
    margin-bottom: 30px;
  }
  &__mail{
    text-decoration-line: underline;
    color: #727272;
  }
  .header__call{
    margin-top: 20px;
    border: 1px solid #DDDDDD;
  }
  &__conf,
  &__c{
    font-weight: 500;
    font-size: 10px;
    line-height: 180%;
    text-transform: uppercase;
    color: #727272;
  }
  &__conf{
    max-width: 500px;
    a{
      text-decoration: underline;
    }
  }
}

.footer-top{
  padding: 52px 0;
  border-top: 1px solid #EBEBEB;
  border-bottom: 1px solid #EBEBEB;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-bottom{
  padding: 27px 0 35px;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}


@media screen and (max-width: 1200px){
  .footer-top{
    &__inner{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 50px 20px;
    }
    .footer__col{
      &:nth-child(1){
        grid-column: 1/-1;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .footer{
    &__links{
      flex-direction: column;
    }
    &__link{
      margin-right: auto !important;
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    &__c{
      margin-top: 15px;
    }
  }

  .footer-top{
    padding: 30px 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 40px;
    }
  }

  .footer-bottom{
    padding: 20px 0;
    &__inner{
      display: block;
    }
  }
}
