.header{
  padding: 13px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__logo{
    display: block;
    width: 120px;
    flex-shrink: 0;
    margin-right: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__menu{
    margin: 0 72px;
    padding: 47px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }
  &__call{
    min-width: 254px;
    height: 60px;
    flex-shrink: 0;
    background: #FFFFFF;
    display: flex;
    flex-direction: column;
    align-items: unset;
    text-align: left;
    padding: 15px 84px 12px 23px;
    text-transform: uppercase;
    position: relative;
    &-title{
      font-weight: 500;
      font-size: 11px;
      line-height: 14px;
      color: #A2A2A2;
    }
    &-desc{
      font-weight: 700;
      font-size: 15px;
      line-height: 18px;
      margin-top: 1px;
    }
    &-icon{
      position: absolute;
      top: 0;
      right: 0;
      width: 60px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $primary;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: #FFFFFF;
    &:not(:last-child){
      margin-right: 35px;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 1350px){
  .header{
    &__menu{
      margin: 0 40px;
    }
  }

  .nav{
    &__link{
      &:not(:last-child){
        margin-right: 15px;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .header{
    &__logo{
      margin: 0;
    }
    &__menu{
      margin: 0 auto;
      padding: 30px;
    }
    .header__call{
      padding: 0;
      width: 60px;
      height: 60px;
      min-width: unset;
      background-color: $primary;
      justify-content: center;
      align-items: center;
      &:hover{
        background-color: darken($primary,10%);
      }
      &-icon{
        width: unset;
        height: unset;
        position: static;
        background-color: transparent;
      }
      &-title,
      &-desc{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      margin-right: auto;
      width: 90px;
    }
    .header__call{
      width: 45px;
      height: 45px;
      svg{
        width: 18px;
        height: auto;
      }
    }
    &__menu{
      overflow-y: auto;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      z-index: 4;
      border: none;
      background-color: #fff;
      margin: 0;
      padding: 120px 30px 30px;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &._open-menu{
      .header{
        &__logo,
        &__call{
          position: relative;
          z-index: 5;
        }
        &__menu{
          opacity: 1;
          pointer-events: unset;
        }
      }
      .burger{
        span{
          background-color: $primary;
          &:nth-child(1){
            transform: rotate(-45deg) translate(-8px,5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-8px,-5px);
          }
        }
      }
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      color: $primary;
      &:not(:last-child){
        margin: 0 0 20px;
      }
    }
  }

  .burger{
    width: 24px;
    height: 20px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 15;
    flex-shrink: 0;
    margin-left: 20px;
    span{
      display: block;
      width: 100%;
      height: 1.31px;
      background: #fff;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
      }
    }
  }
}
