.display1{
  font-size: 64px;
  line-height: 124%;
  font-weight: 500;
  text-transform: uppercase;
}

.display2{
  font-weight: 700;
  font-size: 36px;
  line-height: 123%;
  text-transform: uppercase;
}

.display3{
  font-weight: 700;
  font-size: 24px;
  line-height: 180%;
  text-transform: uppercase;
}

.body-text18{
  font-size: 18px;
  line-height: 180%;
}

.body-text12{
  font-size: 12px;
  line-height: 15px;
}

.body-text{
  font-size: 16px;
  line-height: 125%;
}

.body-text18,
.body-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
}

@media screen and (max-width: 992px){
  .display1{
    font-size: 48px;
  }

  .display2{
    font-size: 27px;
  }
}


@media screen and (max-width: 768px){
  .display1{
    font-size: 32px;
  }

  .display2{
    font-size: 24px;
  }

  .display3{
    font-size: 18px;
  }

  .body-text18{
    font-size: 16px;
  }
}
