@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 30px;
    height: 58px;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    svg{
        &:nth-child(2){
            margin-left: 20px;
        }
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.m-btn-primary-outline{
    color: #fff;
    background-color: transparent;
    border: 1px solid $primary;
    &:hover{
        color: #fff;
        background-color: $primary;
    }
}
