@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  position: static;
  margin: 15px 0;
  &-bullet{
    margin: 0 15px !important;
    width: 6px;
    height: 6px;
    background: #D9D9D9;
    border-radius: 50%;
    opacity: 1;
    &-active{
      background: $black;
    }
  }
}
