@import '../vars';
.modal {
    background: rgba($black, .8);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    @media screen and (min-width: 577px) {
        text-align: center;
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
        &__dialog {
            display: inline-block;
            text-align: left;
            vertical-align: middle;
        }
    }
    &__dialog{
        background-color: #fff;
        width: 550px;
        max-width: 100%;
        padding: 54px 50px 71px;
        position: relative;
    }
    &__close{
        position: absolute;
        left: 100%;
        bottom: 100%;
        transform: translate(6px,-13px);
        background-color: transparent;
    }
    .consult-form{
        box-shadow: none;
        padding: 0;
        background-color: transparent;
        max-width: 365px;
        margin: 0 auto;
        &__grid{
            grid-template-columns: 100%;
            gap: 24px;
        }
        &__btn{
            margin-top: 21px;
        }
    }
}




@media screen and (max-width: 768px){
    .modal{
        &__close{
            top: 20px;
            right: 20px;
            left: unset;
            bottom: unset;
            transform: unset;
            line{
                stroke: $black;
            }
        }
        &__dialog{
            padding-left: 20px;
            padding-right: 20px;
            padding-bottom: 30px;
        }
    }
}
