.catalog-card{
  padding-bottom: 100%;
  position: relative;
  &:hover{
    &:before{
      background: linear-gradient(180deg, rgba(217, 217, 217, 0) 0%, rgba(209, 209, 209, 0) 0.01%, rgba(151, 105, 62, 0.9) 79.69%);
    }
    .catalog-card__nav{
      height: 74px;
    }
  }
  &:before{
    content: '';
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    pointer-events: none;
    background: linear-gradient(180deg, rgba(217, 217, 217, 0) 0%, rgba(209, 209, 209, 0) 0.01%, #0B0B0B 100%);
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 3;
    color: #fff;
  }
  &__title{
    padding: 0 38px;
    font-weight: 700;
    font-size: 32px;
    line-height: 180%;
  }
  &__desc{
    padding: 0 38px;
    font-weight: 500;
    font-size: 16px;
    line-height: 180%;
    display: flex;
    margin: 25px 0 35px;
    &:before{
      content: '';
      width: 57px;
      height: 3px;
      flex-shrink: 0;
      display: block;
      background: #D9D9D9;
      transform: translateY(10px);
      margin-right: 25px;
    }
  }
  &__nav{
    height: 0;
    overflow: hidden;
    transition: all .3s;
  }
  &__btn{
    width: 350px;
    max-width: 100%;
    height: 74px;
    margin-left: auto;
    @media screen and (min-width: 577px){
      color: $primary;
      background: #FFFFFF;
    }
  }
}

.step-card{
  @media screen and (min-width: 769px){
    &._reverse{
      .step-card{
        &__line{
          img{
            left: 0;
            right: unset;
            transform: translateY(-50%) matrix(-1, 0, 0, 1, 0, 0);
          }
        }
      }
    }
    &._third{
      position: relative;
      &:after{
        content: '';
        position: absolute;
        right: 0;
        top: 32px;
        width: 2px;
        height: calc(100% + 53px);
        opacity: 0.5;
        background-color: $primary;
      }
      .step-card{
        &__line{
          height: 2px;
          opacity: 0.5;
          background-color: $primary;
          transform: translateY(-15px);
          img{
            display: none;
          }
        }
      }
    }
    &._last{
      .step-card__title{
        color: $primary;
      }
    }
  }
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__num{
    font-weight: 900;
    font-size: 40px;
    line-height: 49px;
    color: $primary;
    margin-right: 10px;
    flex-shrink: 0;
  }
  &__line{
    flex-grow: 1;
    height: 30px;
    position: relative;
    overflow: hidden;
    img{
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      position: absolute;
    }
  }
  &__title{
    margin-top: 7px;
    text-transform: unset;
    line-height: 125%;
  }
  &__desc{
    margin-top: 6px;
  }
}

.advantage-card{
  padding: 27px 50px 72px 57px;
  background: #F3F3F3;
  position: relative;
  overflow: hidden;
  min-height: 372px;
  &._brown{
    background-color: $primary;
    color: #FFFFFF;
    .advantage-card__text{
      color: #FFFFFF;
      opacity: 0.2;
    }
  }
  &._white{
    background-color: transparent;
  }
  &__bg{
    position: absolute;
    right: 0;
    bottom: 17px;
    width: 39%;
  }
  &__title,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__title{
    font-weight: 400;
    margin-top: 26px;
    text-transform: unset;
    line-height: 125%;
    min-height: 60px;
    b{
      font-weight: 700;
    }
  }
  &__desc{
    margin-top: 21px;
    line-height: 180%;
  }
  &__text{
    position: absolute;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #D7D7D7;
    line-height: 130%;
    left: 14px;
    top: 100px;
    span{
      writing-mode: tb-rl;
      transform: rotate(-180deg) translate(-5px,-10px);
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-card{
    &__nav{
      height: unset;
      overflow: unset;
    }
    &__title{
      font-size: 24px;
    }
  }
}


@media screen and (max-width: 768px){
  .advantage-card{
    padding-right: 20px;
    &__title{
      min-height: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-card{
    padding: 0;
    &:before{
      display: none;
    }
    &__img{
      position: static;
      height: unset;
      object-fit: unset;
    }
    &__content{
      position: static;
      color: $black;
      margin-top: 15px;
    }
    &__title{
      padding: 0;
    }
    &__desc{
      margin: 10px 0 15px;
      padding: 0;
      &:before{
        display: none;
      }
    }
    &__btn{
      height: 58px;
    }
  }
}
