.main{
  padding: 204px 0 12.24%;
  background-image: url(../img/main-bg.png);
  background-size: cover;
  background-color: #000;
  background-position: center;
  color: #fff;
  &__title{
    text-align: center;
  }
  &__subtitle{
    text-align: center;
    font-weight: 400;
  }
  &__desc{
    margin: 38px auto 0;
    max-width: 700px;
    text-align: center;
    font-size: 20px;
    line-height: 125%;
  }
  &__items{
    display: flex;
    justify-content: center;
    margin-top: 5.52%;
  }
  &__item{
    display: flex;
    flex-direction: column;
    padding-left: 56px;
    border-left: 1px solid rgba(255, 255, 255, 0.2);
    width: 290px;
    max-width: calc(100% / 3);
    &:first-child{
      padding-left: 0;
      border: none;
      width: unset;
      padding-right: 27px;
    }
    &-icon{
      height: 40px;
      margin-bottom: 24px;
      img{
        height: 100%;
        width: auto;
      }
    }
    &-title{
      font-weight: 500;
      font-size: 16px;
      line-height: 125%;
      text-transform: uppercase;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 4.57%;
  }
  &__btn{
    &:not(:last-child){
      margin-right: 7px;
    }
  }
}

.consult{
  margin-top: -7.65%;
  position: relative;
  z-index: 2;
}

.consult-form{
  background: #FFFFFF;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.15);
  max-width: 1052px;
  margin: 0 auto;
  padding: 35px 45px 45px;
  &__title{
    font-weight: 300;
    font-size: 32px;
    line-height: 122%;
    text-align: center;
    text-transform: uppercase;
  }
  &__desc{
    text-align: center;
    margin-top: 10px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    align-items: center;
    gap: 30px;
    margin-top: 56px;
  }
  &__btn{
    padding: 0;
  }
}

.quality{
  padding: 70px 0;
  &__subtitle{
    text-align: center;
    text-transform: uppercase;
    color: #666666;
  }
  &__title{
    margin: 23px auto 0;
    max-width: 855px;
    text-align: center;
  }
  &__inner{
    display: grid;
    grid-template-columns: auto 38.6% auto;
    gap: 15px;
    margin-top: 43px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    padding-top: 15.53%;
  }
  &__info2{
    padding-top: 6.8%;
  }
  &__subtitle2{
    text-transform: uppercase;
    margin-bottom: 23px;
  }
}

.gallery{
  padding-top: 70px;
  &__slider{
    overflow: hidden;
    position: relative;
  }
  .swiper{
    @media screen and (min-width: 993px){
      &-container{
        width: 670px;
        max-width: 100%;
        margin: 0 auto;
      }
      &-slide{
        height: 386px;
        &:not(.swiper-slide-active){
          .gallery__img{
            width: 325px;
          }
        }
        &-prev{
          .gallery__img{
            margin-left: auto;
          }
        }
      }
      &-pagination{
        display: none;
      }
    }
    &-button{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: #FFFFFF;
      &:hover{
        background-color: $primary;
        path{
          fill:#fff;
        }
      }
      &-next{
        right: 30px;
      }
      &-prev{
        left: 30px;
      }
    }
  }
  &__img{
    height: 100%;
    img{
      height: 100%;
      object-fit: cover;
      width: 100%;
    }
  }
}


.consult2,
.consult3{
  @media screen and (min-width: 993px){
    position: relative;
    z-index: 2;
    overflow: hidden;
    .consult-form{
      background-color: transparent;
      box-shadow: none;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        background: #FFFFFF;
        box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.15);
        height: 100%;
        width: calc(100% + 300px);
        top: 0;
        left: 0;
      }
      &__title,
      &__desc,
      &__grid{
        position: relative;
        z-index: 2;
      }
    }
  }
}

.consult2{
  padding-bottom: 45px;
  @media screen and (min-width: 993px){
    margin-top: -40px;
    .consult-form{
      max-width: 915px;
      margin-right: 0;
      padding-left: 65px;
      padding-right: 65px;
    }
  }
}

.consult3{
  padding: 50px 0;
  @media screen and (min-width: 993px){
    margin-top: -40px;
    .consult-form{
      max-width: 900px;
      margin-left: 0;
      padding-left: 85px;
      padding-right: 85px;
      &:before{
        left: unset;
        right: 0;
      }
    }
  }
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__info{
    width: 400px;
    margin-left: 85px;
    max-width: 100%;
    flex-shrink: 0;
    padding: 15px 0;
  }
  &__text{
    margin-top: 35px;
    line-height: 123%;
  }
}

.catalog{
  padding: 50px 0;
  &__top{
    display: flex;
    justify-content: space-between;
    margin-top: 23px;
  }
  &__title{
    max-width: 535px;
  }
  &__desc{
    max-width: 555px;
  }
  &__grid{
    margin-top: 90px;
    display: grid;
    grid-template-columns: repeat(2,50%);
  }
}

.objects{
  padding: 50px 0;
  &__title{
    margin-top: 23px;
  }
  &__grid{
    display: grid;
    grid-template-columns: 23.9% 32.4% auto;
    gap: 20px;
    margin-top: 90px;
  }
  &__text{
    line-height: 120%;
    padding: 0 45px;
    &:not(:last-child){
      margin-bottom: 64px;
    }
  }
  &__col2{
    margin-top: -10.9%;
  }
  &__col3{
    margin-top: -31.03%;
  }
}

.object-card{
  position: relative;
  &:not(:last-child){
    margin-bottom: 20px;
  }
  @media screen and (min-width: 993px){
    &--plus{
      width: 126%;
    }
    &--minus{
      width: 79.4%;
      margin-left: auto;
    }
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 20px 30px;
    background: #FFFFFF;
    width: 100%;
    max-width: 325px;
  }
  &__desc{
    margin-top: 14px;
    display: flex;
    &:before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      margin-right: 28px;
      flex-shrink: 0;
      background-color: $black;
      transform: translateY(7px);
    }
  }
}

.steps{
  padding: 50px 0 0;
  &__title{
    margin-top: 23px;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 50px 22px;
    margin-top: 75px;
  }
}

.consult4{
  padding: 50px 0;
  &__btn{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 130px;
    height: 130px;
    border-radius: 50%;
  }
  &__img{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      background: linear-gradient(270deg, #FFFFFF 15.3%, rgba(255, 255, 255, 0) 68.49%);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  @media screen and (min-width: 993px){
    &__inner{
      display: grid;
      grid-template-columns: 32.54% 70%;
      padding-right: 13px;
      background: #FFFFFF;
      box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.15);
    }
    .consult-form{
      box-shadow: none;
      padding: 60px;
      background-color: transparent;
      margin: 0;
    }
  }
}

.advantages{
  padding: 50px 0;
  &__subtitle{
    text-align: center;
  }
  &__title{
    text-align: center;
    margin: 23px auto 0;
    max-width: 800px;
  }
  &__grid{
    margin-top: 52px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}

.work{
  padding: 50px 0;
  &__title{
    margin-top: 23px;
  }
  &__grid{
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
  }
  &__info{
    padding-top: 12.73%;
    ul{
      font-size: 16px;
      line-height: 180%;
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      li{
        line-height: inherit;
        display: flex;
        &:before{
          content: '';
          width: 6px;
          height: 6px;
          display: block;
          background-color: $black;
          border-radius: 50%;
          margin-right: 25px;
          transform: translateY(11.5px);
          flex-shrink: 0;
        }
      }
    }
  }
  &__text{
    line-height: 180%;
    text-transform: uppercase;
    max-width: 350px;
  }
  &__btn{
    color: $primary;
    margin-top: 45px;
  }
  &__items{
    display: grid;
    grid-template-columns: 1.1fr 1fr;
    gap: 40px;
    margin-top: 69px;
  }
  &__item{
    &-text{
      font-size: 14px;
      line-height: 180%;
      text-transform: uppercase;
    }
    &-num{
      font-size: 48px;
      margin: 10px 0;
    }
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__desc{
    margin-top: 26px;
    font-size: 14px;
    line-height: 180%;
    text-transform: uppercase;
  }
}

.what-get{
  padding: 50px 0;
  &__inner{
    display: grid;
    grid-template-columns: 49.3% auto;
    gap: 50px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    padding: 33px 0 0;
  }
  &__title{
    margin-top: 23px;
  }
  &__text{
    margin-top: 54px;
    max-width: 401px;
  }
  .consult4__btn{
    position: static;
    margin: 40px auto 0;
    transform: unset;
  }
}

.order{
  padding: 75px 0 200px;
  &__inner{
    display: grid;
    grid-template-columns: 50% 40.5%;
    justify-content: space-between;
    position: relative;
  }
  &__img{
    position: absolute;
    width: 35.81%;
    right: 40%;
    top: 165px;
  }
  &__left,
  &__right{
    position: relative;
    z-index: 2;
  }
  &__right{
    padding-top: 31px;
  }
  @media screen and (min-width: 1201px) {
    .consult-form {
      padding: 53px 50px 76px;

      &__desc {
        margin: 0;
      }

      &__grid {
        grid-template-columns: 100%;
        max-width: 365px;
        width: 100%;
        margin: 55px auto 0;
      }
    }
  }
  &__title{
    margin-top: 23px;
  }
  &__desc{
    margin-top: 41px;
    max-width: 47%;
  }
}


@media screen and (max-width: 1200px){
  .quality{
    &__inner{
      grid-template-columns: 1fr 1fr;
    }
    &__img{
      order: 2;
      grid-column: 1/-1;
    }
    &__info2{
      order: -1;
    }
  }

  .consult3{
    &__inner{
      display: block;
    }
    &__info{
      margin: 20px 0 0;
      text-align: center;
      width: 100%;
    }
  }

  .object-card{
    &__content{
      padding: 10px;
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: repeat(2,50%);
    }
  }

  .work{
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
    &__info2{
      grid-column: 1/-1;
    }
  }

  .what-get{
    &__inner{
      gap: 20px;
    }
    &__right{
      padding: 0;
    }
  }

  .order{
    padding: 50px 0 100px;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-top: 170px;
    &__desc{
      font-size: 18px;
      margin-top: 30px;
    }
  }

  .consult,
  .consult2{
    margin: 0;
    padding: 0;
    overflow: unset;
    .container{
      padding: 0;
    }
  }

  .consult-form{
    padding: 20px;
    &__title{
      font-size: 27px;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 30px;
      gap: 15px;
    }
  }

  .quality{
    padding: 35px 0;
    &__inner{
      margin-top: 20px;
      grid-template-columns: 100%;
    }
    &__info,
    &__info2{
      padding: 0;
    }
    &__subtitle2{
      margin-bottom: 15px;
    }
  }

  .gallery{
    overflow: hidden;
    padding-top: 35px;
    &__img{
      position: relative;
      height: unset;
      padding-bottom: 70%;
      img{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__slider{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 600px;
        max-width: calc(100% - 25px);
      }
      &-button{
        display: none;
      }
    }
  }

  .catalog{
    padding: 35px 0;
    &__top{
      display: block;
    }
    &__desc{
      margin-top: 10px;
    }
    &__grid{
      margin-top: 25px;
      grid-template-columns: 100%;
    }
  }

  .consult3{
    padding: 35px 0;
    .container{
      padding: 0;
    }
    &__info{
      padding: 0 20px;
    }
  }

  .objects{
    padding: 35px 0;
    &__grid{
      margin-top: 25px;
      grid-template-columns: 100%;
    }
    &__col2,
    &__col3{
      margin: 0;
    }
  }

  .steps{
    padding-top: 35px;
    &__grid{
      margin-top: 20px;
    }
  }

  .consult4{
    padding: 35px 0 0;
    &__btn{
      width: 70px;
      height: 70px;
    }
    .consult-form{
      margin-left: -20px;
      width: calc(100% + 40px);
    }
  }

  .advantages{
    padding: 35px 0;
    &__grid{
      margin-top: 25px;
    }
  }

  .work{
    padding: 35px 0;
    &__info{
      padding: 0;
    }
    &__grid{
      margin-top: 25px;
      grid-template-columns: 100%;
    }
    &__text{
      max-width: 100%;
    }
    &__items{
      margin-top: 30px;
    }
  }

  .what-get {
    padding: 35px 0;
    &__inner {
      grid-template-columns: 100%;
    }
    &__text{
      margin-top: 20px;
    }
    .consult4__btn{
      margin-top: 25px;
    }
  }

  .order{
    padding: 35px 0 70px;
    &__title{
      br{
        display: none;
      }
    }
    &__desc{
      margin-top: 20px;
      max-width: 100%;
    }
    .consult-form{
      margin-left: -20px;
      width: calc(100% + 40px);
    }
  }
}


@media screen and (max-width: 768px){
  .main{
    padding: 120px 0 40px;
    &__desc{
      font-size: 16px;
      margin-top: 20px;
    }
    &__items{
      flex-direction: column;
      align-items: center;
    }
    &__item{
      text-align: center;
      max-width: unset;
      width: unset;
      border: none;
      padding: 0 !important;
      &-icon{
        margin: 0 auto 10px;
      }
      &:not(:last-child){
        margin-bottom: 30px;
      }
      br{
        display: none;
      }
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__btn{
      width: 100%;
      margin: 0 !important;
    }
  }

  .consult-form{
    &__title{
      font-size: 24px;
    }
  }

  .quality{
    &__title{
      font-size: 20px;
    }
  }

  .object-card{
    &__img{
      height: unset;
    }
    &__content{
      position: static;
      max-width: unset;
    }
  }

  .steps{
    &__grid{
      grid-template-columns: 100%;
      gap: 25px;
    }
    .step-card{
      &:nth-child(5){
        order: 2;
      }
      &:nth-child(4){
        order: 3;
      }
    }
  }

  .advantages{
    &__title{
      br{
        display: none;
      }
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .work{
    &__items{
      grid-template-columns: 100%;
      gap: 25px;
    }
    &__btn{
      margin-top: 30px;
    }
    &__desc{
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 576px){
  .catalog{
    &__grid{
      gap: 20px;
    }
  }
}


